import React, { useEffect } from 'react'
import { Link, useParams, useLocation, useNavigate } from 'react-router-dom'
import { useDispatch, useSelector } from 'react-redux'
import { Row, Col, ListGroup, Image, Form, Button, Card } from 'react-bootstrap'
import Message from '../components/Message'
import { addToCart, removeFromCart } from '../actions/cartActions'

const CartScreen = ({match, history}) => {
  
  const {id} = useParams();
  //const productId = match.params.id

  const location = useLocation();

  const qty = location.search ? Number (location.search.split('=') [1]) : 1

  const dispatch = useDispatch()
  const navigate = useNavigate()

  const cart = useSelector((state) => state.cart)
  const { cartItems } = cart

  useEffect(() => {
    if(id){
      //useEffect  working fine
      dispatch(addToCart(id,qty))
    }else{
      //console.log('useEffect error')
    }
  }, [dispatch, id, qty])

  const removeFromCartHandler = (id) => {
    dispatch(removeFromCart(id))
  }

  const chechoutHandler = () => {
    navigate('/login?redirect=/shipping')
  }
  
  return (
  <Row>
    <Col md={8}>
      <h1>Shopping Cart</h1>
      
      { cartItems && cartItems.length === 0 ? (
        <Message> Your cart is empty <Link to='/'>Go Back</Link></Message> 
      ) : (
        <ListGroup variant='flush'> 
            {cartItems.map(item => (
              <ListGroup.Item key={item.product}>
                <Row>
                  <Col md={2}>
                    <Image src={item.image} alt={item.name} className="cartItemPhoto" flude rounded />
                  </Col>
                  <Col md={3}>
                    <Link to={`/product/${item.product}`}>{item.name}</Link>
                  </Col>
                  <Col md={2}>₹{item.price}</Col>
                  <Col md={2}>
                    <Form.Control 
                      as='select' 
                      value={item.qty} 
                      onChange={(e)=> dispatch(addToCart(item.product,Number(e.target.value)))}
                    >

                    {[...Array(item.countInStock).keys()].map((x) => (
                        <option key={x+1} value={x + 1}>
                            { x + 1 }
                        </option>
                    ))}

                    </Form.Control>
                  </Col>
                  <Col md={2}>
                    <Button type='button' varient='ligth' onClick={() => removeFromCartHandler(item.product)} >
                      <i className='fas fa-trash'></i>
                    </Button>
                  </Col>
                </Row>
              </ListGroup.Item>
            ))}
          </ListGroup>
      )}
      
    </Col>
    <Col md={4}>
      <Card>
        <ListGroup varient = 'flush'>
          <ListGroup.Item>
            <h2>Subtotal ({cartItems.reduce((acc,item)=>acc+item.qty,0)}) items</h2>
            ₹{cartItems.reduce((acc,item)=>acc+item.qty * item.price,0).toFixed(2)}
          </ListGroup.Item>
          <ListGroup>
            <Button type='button' className='btn-block' disabled={cartItems.length === 0} onClick={chechoutHandler}> 
              Proceed To Checkout
            </Button>
          </ListGroup>
        </ListGroup>
      </Card>
    </Col>

  </Row>
  )
}

export default CartScreen
