import React, { useEffect, useState } from 'react'
import axios from 'axios'
import {PayPalButton} from 'react-paypal-button-v2'
import {useNavigate, Link, useParams } from 'react-router-dom'
import { Button, Row, Col, ListGroup, Image, Card } from 'react-bootstrap'
import { useDispatch, useSelector } from 'react-redux'
import Message from '../components/Message'
import Loader from '../components/Loader'
//import { CheckoutSteps } from '../components/CheckoutSteps'
import { getOrderDetails, payOrder, deliverOrder } from '../actions/orderActions'
import { ORDER_PAY_RESET, ORDER_DELIVER_RESET } from '../constants/orderConstants'


const OrderScreen = ({ }) => {

    const { id } = useParams();
    const orderId = id

    const [SdkReady, setSdkReady] = useState (false)

    const dispatch = useDispatch()

        
    const navigate = useNavigate();
    const orderDetails = useSelector(state => state.orderDetails)
    const { order, loading, error } = orderDetails

    const orderPay = useSelector(state => state.orderPay)
    const { loading: loadingPay, success:successPay } = orderPay

    const orderDeliver = useSelector((state) => state.orderDeliver)
    const { loading: loadingDeliver, success: successDeliver } = orderDeliver

    const userLogin = useSelector((state) => state.userLogin)
    const { userInfo } = userLogin



    if(!loading){
        /*
        const addDecimals = (num) => {
            return (Math.round(num * 100) / 100).toFixed(2)
        }
         // calculate price
        order.itemsPrice = addDecimals(order.orderItems.reduce(
            (acc, item) => acc + item.price * item.qty, 0
        ))
        order.shippingPrice = 10.0
        order.totalPrice = (Number(order.itemsPrice) + Number(order.shippingPrice)).toFixed(2)
        */
    }


    useEffect(() => {

        if(!userInfo){
            navigate('/login')
        }

        const addPayPalScript = async () => {
            const { data: clientId } = await axios.get('/api/config/paypal')
            const script = document.createElement('script')
            script.type = 'text/javascript'
            script.src = `https://www.paypal.com/sdk/js?client-id=${clientId}`
            //<script src="https://www.paypal.com/sdk/js?client-id=YOUR_CLIENT_ID"></script> 
            script.async = true
            script.onload = () => {
              setSdkReady(true)
            }
            document.body.appendChild(script)
        }

        if(!order || successPay || successDeliver){
            dispatch ({type: ORDER_PAY_RESET })
            dispatch({ type: ORDER_DELIVER_RESET})
            dispatch(getOrderDetails(orderId))
        }
        else if(!order.isPaid){
            if(!window.paypal){
                addPayPalScript()
            }
            else{
                setSdkReady(true)
            }
        }

        // if(!order || order._id !== orderId) { 
        //     dispatch(getOrderDetails(orderId))
        // }
    }, [order, orderId, navigate, successDeliver, successPay, dispatch, userInfo])


    const successPaymentHandler = (paymentResult) => {
        console.log(paymentResult)
        dispatch(payOrder(orderId, paymentResult))
    }

    const deliverHandler = () => {
        dispatch(deliverOrder(order))
    }


    return loading ? <Loader/> : error ? <Message varient = 'danger'>{error}</Message> :
    <>
        <h1>Order {order._id}</h1>
        <Row>
                <Col md={8}>
                    <ListGroup varient='flush'>
                        <ListGroup.Item>
                            <h2>Shipping</h2>
                            <p><strong>Name: </strong> {order.user.name}</p>
                            <p><strong>Email: </strong> <a href={`mailto:${order.user.email}`}>{order.user.email}</a></p>

                            <p>
                                <strong>Address:</strong>
                                {' '}
                                {order.shippingAddress.city},{' '}
                                {order.shippingAddress.postalCode},{' '}
                                {order.shippingAddress.country}
                            </p>

                            {order.isDeliverd ? (
                                <Message varient='success'>Delivered on {order.deliveredAt}</Message>
                            ):(
                                <Message varient='danger'>Not Delivered</Message>
                            )}

                        </ListGroup.Item>


                        <ListGroup.Item>
                            <h2>Payment Method</h2>
                            <p>
                                <strong>Method:</strong>
                                {' '}
                                {order.paymentMethod}
                            </p>


                            {order.isPaid ? (
                                <Message varient='success'>Paid on {order.paidAt}</Message>
                            ):(
                                <Message varient='danger'>Not Paid</Message>
                            )}

                        </ListGroup.Item>


                        <ListGroup.Item>
                            <h2>Order Items</h2>
                            {order.orderItems.length === 0 ? <Message>Order is empty</Message>
                                : (
                                    <ListGroup varient='flush'>
                                        {order.orderItems.map((item, index) => (
                                            <ListGroup.Item key={index}>
                                                <Row>
                                                    <Col md={1}>
                                                        <Image src={item.image} alt={item.name} fluid rounded />
                                                    </Col>
                                                    <Col>
                                                        <Link to={`/product/${item.product}`}>
                                                            {item.name}
                                                        </Link>
                                                    </Col>
                                                    <Col md={4}>
                                                        {item.qty} x ₹{item.price} = ₹{(item.qty * item.price).toFixed(2)}
                                                    </Col>
                                                </Row>
                                            </ListGroup.Item>
                                        ))}
                                    </ListGroup>
                                )}

                        </ListGroup.Item>

                    </ListGroup>
                </Col>
                <Col md={4}>
                    <Card>
                        <ListGroup variant='flush'>
                            <ListGroup.Item>
                                <h2>Order Summary</h2>
                            </ListGroup.Item>
                            <ListGroup.Item>
                                <Row>
                                    <Col>Item</Col>
                                    <Col>₹{order.itemsPrice}</Col>
                                </Row>
                            </ListGroup.Item>
                            <ListGroup.Item>
                                <Row>
                                    <Col>Shipping</Col>
                                    <Col>₹{order.shippingPrice}</Col>
                                </Row>
                            </ListGroup.Item>
                            <ListGroup.Item>
                                <Row>
                                    <Col>Total</Col>
                                    <Col>₹{order.totalPrice}</Col>
                                </Row>
                            </ListGroup.Item>
                            {!order.isPaid && (
                                <ListGroup.Item>
                                    {loadingPay && <Loader/>}
                                    {!SdkReady ? <Loader/>: (
                                        <PayPalButton amount={order.totalPrice} onSuccess={successPaymentHandler}/>
                                    )}
                                </ListGroup.Item>
                            )}

                            {loadingDeliver && <Loader/>}
                            {   userInfo &&
                                userInfo.isAdmin &&
                                //order.isPaid &&
                                !order.isDelivered && (
                                <ListGroup.Item>
                                    <Button
                                    type='button'
                                    className='btn btn-block'
                                    onClick={deliverHandler}
                                    >
                                    Mark As Delivered
                                    </Button>
                                </ListGroup.Item>
                            )}

                        </ListGroup>
                    </Card>
                </Col>
            </Row>
    </>

}

export default OrderScreen